import { uniq } from 'lodash'
import { WalletBlock } from '../wallet-block.model'

/**
 * Get wallet blocks uniq keys
 * @param walletBlocks - the walletBlocks array
 * @returns the array keys
 */
export function getWalletBlocksKeys<T = string>(
  walletBlocks: WalletBlock[],
  key: keyof WalletBlock
): T[] {
  return uniq(walletBlocks.filter((w) => !!w[key]).map((w) => w[key] as T))
}
